var alasql = require('alasql');

var abilities = require('../dataset/abilities.json');

var birthDate = require('../dataset/birthDate.json');

var character = require('../dataset/character.json');

var child = require('../dataset/child.json');

var citizenship = require('../dataset/citizenship.json');

var fictionalUniverse = require('../dataset/fictionalUniverse.json');

var gender = require('../dataset/gender.json');

var memberOf = require('../dataset/memberOf.json');

var occupation = require('../dataset/occupation.json');

var partner = require('../dataset/partner.json');

var presentInWork = require('../dataset/presentInWork.json');

function ComicsDB() {
  var tables = [{
    name: 'abilities',
    data: abilities
  }, {
    name: 'birthDate',
    data: birthDate
  }, {
    name: 'character',
    data: character
  }, {
    name: 'child',
    data: child
  }, {
    name: 'citizenship',
    data: citizenship
  }, {
    name: 'fictionalUniverse',
    data: fictionalUniverse
  }, {
    name: 'gender',
    data: gender
  }, {
    name: 'memberOf',
    data: memberOf
  }, {
    name: 'occupation',
    data: occupation
  }, {
    name: 'partner',
    data: partner
  }, {
    name: 'presentInWork',
    data: presentInWork
  }];
  var db = new alasql.Database();
  tables.forEach(function (table) {
    db.exec("CREATE TABLE ".concat(table.name));
    db.exec("INSERT INTO ".concat(table.name, " SELECT * FROM ?"), [table.data]);
  });
  return db;
}

var comicsDB = ComicsDB();
module.exports = comicsDB;